import React, { Component } from "react";
import gql from "graphql-tag";
import { Query } from "react-apollo";
import styled from "styled-components";
import Error from "./ErrorMessage";
import Head from "next/head";

const CategoryStyles = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  max-width: ${props => props.theme.maxWidth};
  margin: 0 auto;
  color: black;
`;

const CATEGORY_EVENTS_QUERY = gql`
  query CATEGORY_EVENTS_QUERY($query: String!) {
    meetings(where: { category_contains: $query }) {
      id
      title
      lng
      lat
      startTime
      endTime
      description
      category
      address
      user {
        id
      }
    }
  }
`;

class Category extends Component {
  render() {
    return (
      <Query
        query={CATEGORY_EVENTS_QUERY}
        variables={{
          query: this.props.query
        }}
      >
        {({ error, loading, data }) => {
          if (error) return <Error error={error} />;
          if (loading) return <p>loading....</p>;
          if (!data.meetings) {
            return <p>No Meeting Found for {this.props.category}</p>;
          }
          data.meetings.map(meeting => {
            return (
              <CategoryStyles key={meeting.id}>
                <h2>{meeting.title}</h2>
                {console.log(meeting.title)}
              </CategoryStyles>
            );
          });
        }}
      </Query>
    );
  }
}

export default Category;
